<template>

    <div class="received-loan-container">

        <div class="go-back-link">
            <router-link to="/orders"><span class="icon-arrow-left"></span>Назад</router-link>
        </div>

        <div class="page-title">
            <h1>Кредит «{{receivedLoan.title}}»</h1>
        </div>

        <div class="block">

            <div class="block-title">
                <div class="h2">Информация о кредите</div>
            </div>

            <div class="block-content">

                <div class="received-load-paid-out">
                    <div class="h4">{{parsePay(receivedLoan.paidOut)}} <span class="icon-ruble"></span></div>
                    <div class="received-load-paid-out-line">
                        <span :style="{width: `${receivedLoan.paidOut / receivedLoan.sum * 100}%`}"></span>
                    </div>
                    <p>Выплачено за {{paidOutMonths}}</p>
                </div>

                <div class="received-load-info">
                    <ul>
                        <li>
                            <span>Сумма кредита</span>
                            <span>{{parsePay(receivedLoan.sum)}} <span class="icon-ruble"></span></span>
                        </li>
                        <li>
                            <span>Срок кредита</span>
                            <span>{{monthsDiff}}</span>
                        </li>
                        <li>
                            <span>Процентная ставка</span>
                            <span>{{receivedLoan.percent}}</span>
                        </li>
                        <li>
                            <span>Дата начала</span>
                            <span>{{receivedLoan.startDate}}</span>
                        </li>
                        <li>
                            <span>Дата окончания</span>
                            <span>{{receivedLoan.endDate}}</span>
                        </li>
                    </ul>
                </div>

            </div>
        </div>

    </div>

</template>

<script>

    import moment from 'moment'

    export default {
        name: "ReceivedLoad",
        data: () => ({
            receivedLoan: {
                id: 1,
                title: 'Быстрый!',
                startDate: '01.01.2021',
                endDate: '01.01.2024',
                sum: 38000000,
                percent: '6.6%',
                paidOut: 17000000
            }
        }),
        computed: {
            paidOutMonths() {
                let startDateArr = this.receivedLoan.startDate.split('.').map(x => parseInt(x)),
                    res = Math.round(moment(new Date()).diff(moment([startDateArr[2], startDateArr[1] - 1, startDateArr[0]]), 'months', true)),
                    lastResSymbol = parseInt(res.toString().slice(-1)),
                    monthText;
                if ( lastResSymbol == 1 ) {
                    monthText = 'месяц'
                }
                else if ( lastResSymbol > 1 && lastResSymbol < 5 ) {
                    monthText = 'месяца'
                }
                else if ( lastResSymbol >= 5 || lastResSymbol == 0 ) {
                    monthText = 'месяцев'
                }
                return `${res} ${monthText}`
            },
            monthsDiff() {
                let startDateArr = this.receivedLoan.startDate.split('.').map(x => parseInt(x)),
                    endDateArr = this.receivedLoan.endDate.split('.').map(x => parseInt(x)),
                    res = Math.round(moment([endDateArr[2], endDateArr[1] - 1, endDateArr[0]]).diff(moment([startDateArr[2], startDateArr[1] - 1, startDateArr[0]]), 'months', true)),
                    lastResSymbol = parseInt(res.toString().slice(-1)),
                    monthText;
                if ( lastResSymbol == 1 ) {
                    monthText = 'месяц'
                }
                else if ( lastResSymbol > 1 && lastResSymbol < 5 ) {
                    monthText = 'месяца'
                }
                else if ( lastResSymbol >= 5 || lastResSymbol == 0 ) {
                    monthText = 'месяцев'
                }
                return `${res} ${monthText}`
            }
        },
        methods: {
            parsePay(number) {
                return number.toString().replace(/(\d)(?=(\d{3})+(\D|$))/g, '$1 ')
            },
        }
    }
</script>
